body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #9c9ceb;
}

.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

html {
  line-height: 1.15;
}

.navbar {
  background: #5d5dfd;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0,0,0,.22);
  color: #fff;
  font-size: calc(10px + 14 * (100vw - 320px)/800);
  z-index: 99;
  white-space: nowrap;
  display: block;
  padding-right: 40px;
}

.navbar ul {
  margin: 0;
  padding: 0;
  display: block;
  list-style-type: disc;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.navbar li {
  display: inline-block;
  width: 33.3%;
  text-align: center;
  line-height: 30px;
  padding-right: 10px;
}

.navbar a {
  text-decoration: none;
  color: #fff;
  font-weight: 900;
  background-color: transparent;
  cursor: pointer;
}

.navbar .correct {
  -webkit-animation: .4s correct;
          animation: .4s correct;
}

.navbar .incorrect {
  -webkit-animation: .4s incorrect;
          animation: .4s incorrect;
}

@-webkit-keyframes correct {
  0% {
    color: #fff;
  }
  50% {
    color: #2e7d32;
  }
  100% {
    color: #fff;
  }
}

@keyframes correct {
  0% {
    color: #fff;
  }
  50% {
    color: #2e7d32;
  }
  100% {
    color: #fff;
  }
}

@-webkit-keyframes incorrect {
  0% {
    color: #fff;
  }
  50% {
    color: #c62828;
  }
  100% {
    color: #fff;
  }
}

@keyframes incorrect {
  0% {
    color: #fff;
  }
  50% {
    color: #c62828;
  }
  100% {
    color: #fff;
  }
}

.jumbotron {
  display: block;
  width: 100%;
  background-image: url(/Clicky-Game/static/media/dark-paths.7d917551.png);
  background-repeat: repeat;
  color: #fff;
  padding-top: 140px;
  padding-bottom: 20px;
  text-align: center;
}

.jumbotron h1 {
  display: block;
  margin: 0;
  margin-bottom: 30px;
  font-size: 4rem;
}

.jumbotron h2 {
  display: block;
  font-size: 1.5em;
  font-weight: bold;
}



.click-item {
  background-size: cover;
  background-position: 50%;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
  display: block;
}

.click-item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  transition: .1s;
}

.container {
  display: -webkit-flex;
  display: flex;
  min-height: 80%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  margin: 0 auto;
}

@media (min-width: 1100px) {
  .container {
    padding-left: 100px;
    padding-right: 100px;
    width: 1100px;
  }
}
